import SearchBar from "../../components/colorui/components/searchbar.vue"
import SearchItem from "./components/SearchItem.vue";
import Tabbar from "../../components/colorui/components/bar";
import PopUp from "../../components/popup/popup";
import common from "../../common/common.js"
var serverUrl = common.serverUrl
export default {
	components:{
	SearchItem,
	SearchBar,
	Tabbar,
	PopUp,
	},
	data() {
		return {
			show:false,
			keywords:" ",
			coverData:[],
			selectedIndex:2,
			myPageHelper:{},
			none:true
		}
	},
	onPullDownRefresh(){
		uni.request({
			url:serverUrl+'/articles/search?currentNum=1',
			method:'GET',
			success: (res) => {
				if(res.data.code===0){
					uni.stopPullDownRefresh()
					this.myPageHelper=res.data.data
				}
			}
		})
	},
	onReachBottom() {
		var keywords = this.keywords
		var currentPage = this.myPageHelper.pageNum+1
		var hasNextPage = this.myPageHelper.hasNextPage
		if(hasNextPage){
			if(this.none){
				this.getMoreHomeArticles(currentPage,7)
			}else{
				this.getMoreSearchArticles(keywords,currentPage,7)
			}
		}else{
			return
		}
	},
	onLoad() {},
	onShow() {
		// setTimeout(() => {
		this.show = false;
		// }, 500);
		uni.request({
			url:serverUrl+'/articles/search?currentNum=1',
			method:'GET',
			success: (res) => {
				if(res.data.code===0){
					this.myPageHelper=res.data.data
				}
			}
		})
	},
	onHide() {
		this.show = false;
	},
	methods: {
		getMoreHomeArticles(currentNum,size){
			uni.request({
				header:{
					"Authorization":this.token,
					"type":this.type
				},
				url:serverUrl+'/articles/search?currentNum='+currentNum+'&size='+size,
				method: 'GET',
				success: (res) => {
					if(res.data.code==0){
						var myData = res.data.data
						var oldList = this.myPageHelper.list
						var newList =oldList.concat(myData.list)
						myData.list=newList
						this.myPageHelper = myData
					}
		
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getMoreSearchArticles(keywords,currentNum,size){
			uni.request({
				header:{
					"Authorization":this.token,
					"type":this.type
				},
				url:serverUrl+'/articles/search?keywords='+keywords+'&currentNum='+currentNum+'&size='+size,
				method: 'GET',
				success: (res) => {
					if(res.data.code==0){
						var myData = res.data.data
						var oldList = this.myPageHelper.list
						var newList =oldList.concat(myData.list)
						myData.list=newList
						this.myPageHelper = myData
					}
		
				},
				fail: () => {},
				complete: () => {}
			});
		},
		search(data){
				if(data.length!==0){
					this.none = false
				}
				console.log(this.none)
				if(this.none){
					uni.request({
					url:serverUrl+'/articles/search?currentNum=1',
					method:'GET',
					success: (res) => {
						if(res.data.code===0){
							this.myPageHelper=res.data.data
						}
					}
				})
				return
			}else{
				var keywords = data
				this.keywords = data
				uni.request({
					url:serverUrl+'/articles/search?keywords='+keywords+'&currentNum=1',
					method:'GET',
					success: (res) => {
						if(res.data.code===0){
							this.myPageHelper=res.data.data
						}
					}
				})
			}
				
				
			
			
		},
		pop(data){
			this.show = data;
		}

	}
}
